var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美體雕塑")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("S美人計畫")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"S美人計畫","titleEn":"S-Body"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><img"+(_vm._ssrAttr("src",require("assets/img/service/beautySlim/s-body/banner.jpg")))+" alt=\"S美人計畫\"> <h2>美麗功效</h2> <p>肥胖是女人的天敵，如何維持曼妙的身材，則是女人ㄧ生的課題。減肥（瘦身）是指採用人為手段故意降低體重，特別是減少體內的脂肪。每個人減肥的原因各自不同，主要有1.健康、2.美容、3.參與對體重、外形有限制的職業和活動。除了積極控制飲食和運動減肥外，市面上，還有各種幫助減肥的方式，包括侵入性的手術抽脂和非侵入性的各種物理性化學性溶脂方式。</p></div> <div class=\"article-container\"><h2>打造迷人S曲線</h2> <p>S美人計劃是利用藥物特性，將藥物注射進皮下脂肪層，直接破壞脂肪細胞，並使之乳糜化（變得像液體一樣），於是更容易被體內巨噬細胞帶走吸收，再藉由淋巴管輸送到肝臟，分解為二氧化碳與水，再排出體外。如此一來，便能達到消脂效果。無論是全身性的脂肪堆積或局部的脂肪堆積，這些脂肪都可以被逐步分解、消除。對於消除脂肪堆積與修飾身型，都是一種方便安全的治療選擇。這類藥物最初的用途是治療肝炎、脂肪肝，由於具備破壞脂肪細胞的效用，運用在醫美領域，確實可以達到局部消脂、雕塑身體線條的功效。</p> <h3>強效五合一：雕塑、曲線、活膚、美肌、緊緻</h3> <ul><li>✔ 快: (植物精粹)比單純節食更有效</li> <li>✔ 狠: (全面塑化)適用九成以上成人使用</li> <li>✔ 準: (定點作用)可針對特定部位</li></ul> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautySlim/s-body/1.png")))+" alt=\"強效五合一 : 雕塑、曲線、活膚、美肌、緊緻\" class=\"w-50\"></div> <div class=\"article-container\"><h2>純天然植物萃取</h2> <ul><li>✔ L-酪氨酸:抑制胃口、减少體脂肪</li> <li>✔ 歐洲七葉樹萃取:刺激血液循環，有效改善浮腫</li> <li>✔ 綠茶萃取(兒茶素):降低身體脂肪的堆積，同時減少脂肪細胞的增殖</li> <li>✔ 闊葉山麥冬萃取:保濕、抗菌、防曬</li> <li>✔ 積雪草萃取:亮白、護膚(創傷不留疤痕，改善血 液代謝週期)</li> <li>✔ 甘草萃取:亮白、抗過敏、抑制自由基的形成(抗衰老)、消炎</li> <li>✔ 虎杖萃取:抑菌、抗真菌、抑制酪氨酸酶(亮白)</li> <li>✔ 黃芩萃取:防曬、抑治挫瘡、皮膚病、護膚、抑制脂質過氧化、增強細胞活性</li></ul></div> <div class=\"article-container\"><h2>作用原理</h2> <p>純天然提取的Cf-Line可對皮膚細胞的直接作用。所以它是一個安全的護膚解決方案，讓你迅速體驗自然的變化。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautySlim/s-body/2.jpg")))+" alt=\"作用原理\"> <ul><li>✦ 減少肌膚腫脹發生率，有助於使臉部和身體線條顯得精緻</li> <li>✦ 促進微細血管和淋巴循環，增強皮膚細胞活力及皮膚的彈性</li> <li>✦ 有助於通過活化皮膚細胞的自我繁殖作用，使皮膚細胞再生</li> <li>✦ 縮小脂肪細胞體積，從而擁有更輕盈的臉部和身體線條</li></ul></div> <div class=\"article-container\"><h2>適合部位</h2> <ul><li>✔ 頭部：臉頰、雙下巴、頸部</li> <li>✔ 手部：蝴蝶袖</li> <li>✔ 軀幹：上腹部、下腹部、腰間贅肉</li> <li>✔ 腿部：臀部、大腿</li></ul></div> <div class=\"article-container\"><h2>美麗功效</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautySlim/s-body/3.png")))+" alt=\"美麗功效\"> <ul><li>✦ 創新性療法:與傳統治療相比較，不用開刀和抽脂，免除了患者吸脂手術痛苦、皮膚凹凸不平、減少併發症的發生。</li> <li>✦ 使用後不反彈:是可縮小脂肪， 不是減少水份，脂肪的構成是要經過較長的時間才堆積構成 ， 所以它不易反彈。</li> <li>✦ 無需刻意節食、見效快捷、療效持久。</li> <li>✦ 臉部改善效果好，能緊致鬆弛的皮膚</li> <li>✦ 縮小脂肪細胞體積，從而擁有更輕盈的臉部和身體線條。</li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautySlim/s-body/4.png")))+" alt=\"美麗案例\"></div> <div class=\"article-container\"><h2>注意事項</h2> <ul><li>✔ 未成年或兒童忌用</li> <li>✔ 孕婦和哺乳期婦女忌用</li> <li>✔ 心臟病患者忌用</li> <li>✔ 腎臟病患者忌用</li> <li>✔ 心血管疾病患者忌用</li> <li>✔ 重症治療患者忌用</li> <li>✔ 一些自身免疫系統疾病（如硬皮病、普通的天皰瘡、全身紅斑、皮肌炎等）患者忌用</li> <li>✔ 療程使用前禁止服用抗凝血劑</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>✦ 接受治療後，在診所或者醫院休息30分鐘。</li><li></li><li>✦ 剛治療完後，有些會呈現部分紅、腫、熱、瘀青、脹痛，都屬於正常現象，普通很細微，經冰敷會很快好轉，幾個小時就會消</li><li>逝。\n            </li><li>✦ 24小時內不要清洗注射部位及化妝，避免局部汙染。</li><li></li><li>✦ 治療後第一天；喝大量的水(1500-2000cc)</li><li></li><li>✦ 治療後的第一周，避免陽光照曬，以免引起色素沉澱。</li><li></li><li>✦ 在第一個月內，如果要照曬太陽，請使用防曬霜。</li><li></li><li>✦ 剛打完部位護理人員先用棉球或紗布壓5分鐘，以避免瘀青。</li><li></li><li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div>")],2)}
